//
// user/variables.scss
// This file is mean to overwrite Bootstrap or DashCore variables
// It is recommended you do this way and not by editing the Bootstrap or DashCore variables files because future updates
//

// You can give a Boostrap or DashCore variable any value you like here, e.g
// $body-color: #c3000d; // this will change the default font color

// [ FONTS ]
$font-family-template: 'Poppins', sans-serif !default;
$font-family-complementary: $font-family-template !default;
$font-family-handwritten: "Caveat", cursive, $font-family-template !default;
$font-family-awesome: "Font Awesome 5 Free" !default;

// Updated to Bootstrap 4 fix
$grid-float-breakpoint: md;
$grid-float-breakpoint-max: sm;

// [ COLORS ]
$text-contrast: #ffffff !default;
$text-dark: #646f79 !default; // #4E5A70 (o) BLUE
$color-3: #1abd6f !default; //#7b68ee !default; //#3c1fc7 !default; MID GREEN
$color-4: #2bdaa5 !default; //#7a39db !default; Creamy Green
$color-5: #90e0f3 !default; //#143350 !default; // #181c1f (o) Creamy Blue
$color-6: #f4f8fb !default; // #gray (o)
$color-primary: #1abd6f !default;
$big-mockup-width: 920px;
$color-success: #1abd6f;
$color-info: #f4f8fb;
$navbar-side-text-color: #1abd6f;
$navbar-side-text-color-hover: #1abd6f;
$navbar-side-background-color: #fff;
$navbar-fixed-text-color-hover: #fff;
$navbar-fixed-text-color-active: #fff;
$navbar-text-color: #fff;
$navbar-text-color-hover: #fff;
$navbar-sticky-text-color: #000;
$navbar-sticky-text-color-hover: #000;

$color-scheme: (
    1: $text-contrast,
    2: $text-dark,
    3: $color-3,
    4: $color-4,
    5: $color-5,
    6: $color-6
) !default;

$all-colors: $color-scheme;

// Social icons
//
$brand-colors: () !default;
$brand-colors: map-merge((
    "facebook":   #3b5998,
    "google":     #dd4b39,
    "gplus":      #dd4b39,
    "twitter":    #00aced,
    "linkedin":   #007bb6,
    "pinterest":  #cb2027,
    "git":        #666666,
    "tumblr":     #32506d,
    "vimeo":      #aad450,
    "youtube":    #bb0000,
    "flickr":     #ff0084,
    "reddit":     #ff4500,
    "dribbble":   #ea4c89,
    "skype":      #00aff0,
    "instagram":  #517fa4,
    "lastfm":     #c3000d,
    "soundcloud": #ff8800,
    "behance":    #1769ff,
    "envato":     #82b541,
    "medium":     #00ab6c,
    "spotify":    #1db954,
    "quora":      #a82400,
    "xing":       #026466,
    "snapchat":   #fffc00,
    "telegram":   #0088cc,
    "whatsapp":   #075e54,
    "rss":        #f26522,
), $brand-colors);


$x-scaling: 0.65;
$x-dark-bg: #152975;
$x-light-bg: #f2f2f5;


// gradients
$gradients-map: (
  (
    green: $color-3,
    variants: (
      blue: $color-5,
      dark: $color-5,
      navy: $color-5
    )
  ),
  (
    blue: $color-4,
    variants: (
      purple: $color-3,
      dark: $color-3,
      navy: $color-3
    )
  ),
  (
    navy: $color-4,
    variants: (
      purple: $text-contrast,
      blue: $text-dark
    )
  ),
  (
    primary: $color-primary,
    variants: (
      dark: $text-contrast,
      light: $text-contrast
    )
  )
);